import {useState} from "react";

import {action} from "@storybook/addon-actions";
import {Accordion} from "./Accordion";

export default {
    title:'Accordion',
    component:Accordion
}

const callback = action('On or Of click')

export const On = ()=> <Accordion  collapsed={true} changeCollapsed={callback} title={'Menu'} />;
export const Off = ()=> <Accordion collapsed={false} changeCollapsed={callback} title={'Hello'} />;

export const OnOffChange = ()=> {
    const [collapsed,setCollapsed]=useState(true)
    return (<Accordion collapsed={collapsed} changeCollapsed={()=>setCollapsed(!collapsed)} title={''}/>)
};


On.parameters = { storySource: { source: "()=> <Accordion  collapsed={true} changeCollapsed={callback} title={'Menu'} />" }, ...On.parameters };
Off.parameters = { storySource: { source: "()=> <Accordion collapsed={false} changeCollapsed={callback} title={'Hello'} />" }, ...Off.parameters };
OnOffChange.parameters = { storySource: { source: "()=> {\n    const [collapsed,setCollapsed]=useState(true)\n    return (<Accordion collapsed={collapsed} changeCollapsed={()=>setCollapsed(!collapsed)} title={''}/>)\n}" }, ...OnOffChange.parameters };